body, html{
	padding: 0px;
	margin: 0px;
}
#root{
	position: absolute;
	height: 100%
}

/*b3442e	636863	b49410	d1d1d1	568b98*/

header{
	height: 75px;
	background-color: #1f3a93;
	display: -webkit-inline-flex;
	display: inline-flex;
	width: 100%;
}
#logo-wrapper{
	display: -webkit-flex;
	display: flex;
	float: left;
	margin-left: 20px
}
#nav-wrapper{
	font-family: 'Play', sans-serif;
	width: 96%;
	height: 30px;
	display: -webkit-flex;
	display: flex;
	float: right;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	-webkit-align-self: flex-end;
	        align-self: flex-end;
	font-size: 18px;
}
#logo-wrapper img{
	width: 160px;
	position: absolute;
	z-index: 1;
	top: 0px;
}
header .nav-item{
	width: 100px;
	margin: 0px 1px;
	background-color: #00000044;
	border-top-right-radius: 10px;
	border-top-left-radius: 10px;
	color: white;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: flex-end;
	        align-items: flex-end;
	transition: background-color 0.3s ease;
	cursor: pointer;
	padding-bottom: 3px
}
header .nav-item:hover{
	background-color: #a537fd;
}
header p{ padding: 0px; margin: 0px; }
header i{
	padding: 20px;
	font-size: 6em;
	color: black;
	opacity: 0.6;}


@media all and (min-width: 480px) {
  .Login {
    padding: 60px 0;
  }

  .Login form {
    margin: 0 auto;
    max-width: 320px;
  }
  .forgot-password {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-transform: translateY(-1.5rem);
            transform: translateY(-1.5rem);
  }
  .u-text-center{
    text-align: center;
  }
}
section {
    background: linear-gradient(to bottom, white 70%, #63686355);
    height: 70vh;
    margin: auto;
    text-align: center;
    font-family: "Play", sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    margin-top:-4px;
}

#not-logged-in>div {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-inline-flex;
    display: inline-flex;
}

section h1 {
    padding-top: 200px;
    font-size: 3em;
    margin: 0px;
}

#not-logged-in div div {
    border-radius: 100%;
    width: 150px;
    height: 150px;
    margin: 80px;
    background-color: #f1f1ff;
    transition: background-color 0.2s linear, -webkit-transform 0.2s ease-out;
    transition: transform 0.2s ease-out, background-color 0.2s linear;
    transition: transform 0.2s ease-out, background-color 0.2s linear, -webkit-transform 0.2s ease-out;
}

#not-logged-in div div:hover {
    background-color: #b3442edd;
}

div[aria]:hover:after {
    content: attr(aria);
    display: block;
    font-family: "Play", sans-serif;
    font-size: 20px;
}

#not-logged-in div div:hover {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

section i {
    font-size: 6em;
    padding: 25px 15px;
}

.user-pointer {
    position: absolute;
    font-size: 3em;
    margin-top: 50px;
    margin-left: 70px;
    color: white;
}

a {
    font-weight: bold;
    text-decoration: none;
    color: #000;
}

#signup {
    padding-left: 5px;
    display: block;
    transition: color 0.2s linear, -webkit-transform 0.2s ease-out;
    transition: transform 0.2s ease-out, color 0.2s linear;
    transition: transform 0.2s ease-out, color 0.2s linear, -webkit-transform 0.2s ease-out;
}

#signup:hover {
    color: #b3442e;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

main,
main section {
    width: 100vw;
    height: 90vh;
}

section canvas {
    position: relative !important;
    width: 100% !important;
    height: 100% !important;
}

.mapboxgl-marker i {
    color: #e27cdf;
    font-size: 22px;
    padding: 0px;
    position: relative;
    top: -20px;
    left: -8px;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.mapboxgl-marker #me {
    color: blue;
    font-size: 45px;
    top: -40px;
    left: -18px;

}

.mapboxgl-marker i:hover {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

#popup {
    position: absolute;
    width: 50vw;
    height: 0px;
    bottom: 0px;
    border-radius: 20px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    left: 25%;
    background-color: #1f3a9388;
    color: black;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
    transition: height 0.6s ease-in-out;
}

#popup div{
    text-align: center;
    width: 40%;
    height: 50px;
    margin: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    border-radius: 18px;
    border: none;
    font-weight: bold;
    background-color: #1f3a93aa;
}
#popup div * {
    width: 90%;
    background-color: transparent;
    border: none;
    color: white;
}
#popup div label{
    font-size: 10px;
    text-align: left;
}
#popup div input{
    height: 20px;
    font-size: 15px;
    text-align: center;
    outline: none;
}

#add-new {
    position: absolute;
    width: 50vw;
    height: 0px;
    bottom: 0px;
    border-radius: 20px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    left: 25%;
    background-color: #1f3a9388;
    color: black;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
    transition: height 0.4s ease-in-out;
}

#add-new input {
    height: 30px;
    width: 50%;
    margin: 5px auto;
    border-radius: 15px;
    text-align: center;
}

#add-new input[type="button"] {
    background-color: #1f3a93;
    color: white;
    font-weight: bold;
    border: none;
    height: 40px;
}
#close-btn {
    display:-webkit-flex;
    display:flex;
    width: 40px;
    height: 40px;
    cursor: pointer;
    position: absolute;
    left: 0px;
    top: 0px;
    color: white;
    background-color: #0000ff66;
    border-radius: 50%;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 0px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
